export default {
	methods: {
		/**
		 * 
		 * @param {Object} order_goods_id
		 * @param {Object} callback
		 */
		cancleRefund(order_goods_id, callback) {
			uni.showModal({
				content: ',',
				cancelText: '',
				cancelColor: '#898989',
				success: res => {
					if (res.confirm) {
						this.$api.sendRequest({
							url: '/api/orderrefund/cancel',
							data: {
								order_goods_id
							},
							success: res => {
								typeof callback == 'function' && callback(res);
							}
						})
					}
				}
			})
		},

	}
}
